import { useEffect, useRef } from 'react';

import type { WAVETYPE } from '../State';

export interface FGAudioChannel {
  active: boolean,
  frequency: number,
  amplitude: number,
  waveform: WAVETYPE,
  phase: number,
}

interface FGAudioChannelProps extends FGAudioChannel {
  audioCtx: AudioContext,
  channel: "left" | "right",
  syncMultiplier: number,
  analyser?: AnalyserNode,
}

// Convert from [0, 360] to corresponding amount of delay in seconds
const transformPhase = (frequency: number, phase: number) => {
  return (1/frequency) * (phase/360 + 0.5);
}

export const FunctionGeneratorChannel = (props: FGAudioChannelProps) => {
  // Audio node refs
  const oscillator = useRef<OscillatorNode>(null!);
  const stereo = useRef<StereoPannerNode>(null!);
  const gain = useRef<GainNode>(null!);
  const delay = useRef<DelayNode>(null!);

  // Value refs
  const frequency = useRef<number>(440);
  const amplitude = useRef<number>(100);
  const phase = useRef<number>(180);

  // Activate/deactivate
  useEffect(() => {
    // Is activating?
    if(props.active) {
      // Setup oscillator node
      let newOsc = props.audioCtx.createOscillator();
      newOsc.type = props.waveform;
      newOsc.frequency.value = frequency.current;

      // Setup gain node
      let newGain = props.audioCtx.createGain();
      newGain.gain.value = amplitude.current;

      // Setup stereo node
      let newStereo = props.audioCtx.createStereoPanner();
      let offset: number = 0;
      switch(props.channel) {
        case "left": {
          offset = -1;
          break;
        }
        case "right": {
          offset = 1;
          break;
        }
        default: {
          offset = 0;
          break;
        }
      }

      newStereo.pan.value = offset;

      // Setup delay node
      let newDelay = props.audioCtx.createDelay();
      newDelay.delayTime.value = transformPhase(frequency.current, phase.current);

      // Save to refs
      oscillator.current = newOsc;
      gain.current = newGain;
      stereo.current = newStereo;
      delay.current = newDelay;


      // Connect nodes
      newOsc.connect(newGain);
      newGain.connect(newStereo);
      newStereo.connect(newDelay);
      newDelay.connect(props.audioCtx.destination);

      // Connect to analyser if it exists
      if(props.analyser) {
        newDelay.connect(props.analyser);
      }

      // Start playing at next integer timestamp at least 0.05 seconds from now
      // if the channels can be synced. Otherwise, start immediately.
      if(props.syncMultiplier) {
        newOsc.start(Math.ceil(props.audioCtx.currentTime + 0.05));
      } else {
        newOsc.start();
      }

      // Cleanup function
      return () => {
        newOsc.stop();
        newOsc.disconnect();
        newGain.disconnect();
        newStereo.disconnect();
        newDelay.disconnect();

        if(props.analyser) {
          props.analyser.disconnect();
        }
      };
    }
  }, [props.audioCtx, props.waveform, props.channel, props.active, props.analyser, props.syncMultiplier]);

  // Update parameter values
  useEffect(() => {
    // Update ref
    frequency.current = props.frequency;
    amplitude.current = props.amplitude;
    phase.current = props.phase;

    // If active, also update current oscillator
    if(props.active) {
      oscillator.current.frequency.value = frequency.current;
      gain.current.gain.value = amplitude.current;
      delay.current.delayTime.value = transformPhase(frequency.current, phase.current);
    }
  }, [props.active, props.frequency, props.amplitude, props.phase]);

  return null;
};
